

.questionpar-wraper {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 30px;
}


.questionpar {
    border: solid 2px gray;
    border-radius: 5px;
    padding-top: 30px;
    padding-bottom: 30px;
    width: 90%;
}


.questions {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    row-gap: 30px;
    margin-top: 30px;
}


.question {
    width: 80%;
    height: 100px;
    border: solid 2px gray;
    border-radius: 3px;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.questiontext {
    margin-top: 20px;
    font-size: 18px;
}

.number {
    font-size: 17px;
}

.toptext {
    margin-left: 3%;
}